.choosePeople-box {
  /deep/ .el-dialog {
    width: 900px !important;
  }
  .content-box {
    .content_box {
      display: flex;
      justify-content: space-between;

      .content-left {
        width: 228px;
        margin-right: 14px;

        .select-content-box {
          border: 1px solid #e5e5e5;
          border-radius: 5px;
          height: 410px;
          overflow: hidden;
          margin-top: 10px;

          .organization-box,
          .role-box {
            width: 100%;
            height: 100%;
          }

          .people-box {
            width: 100%;
            height: 100%;
            .peoele-title {
              line-height: 35px;
              padding-left: 10px;
              color: #999999;
            }

            .item-li {
              line-height: 22px;
              padding-left: 20px;
              cursor: pointer;
            }
            .item-li:hover {
              color: #4b77be;
            }
          }
        }
      }

      .content-right {
        flex: 1;

        .content-right-top {
          display: flex;
          justify-content: space-between;

          .selected-person-box {
            position: relative;

            .selected-person {
              position: absolute;
              top: 33px;
              right: 0;
              width: 200px;
              height: 420px;
              background: #ffffff;
              border: 1px solid #e5e5e5;
              border-radius: 5px;
              box-shadow: 0px 4px 8px 2px rgba(0, 0, 0, 0.1);
              z-index: 22;

              ul {
                list-style: none;

                .selected-item {
                  line-height: 20px;
                  padding: 5px 10px;
                }
              }
            }
          }
        }
      }
    }
    .search-box {
      display: flex;

      .el-input {
        width: 200px;
      }

      .search-button {
        margin-left: 5px;
      }
    }

    .table-box {
      margin: 10px 0;
      border: 1px solid #ddd;
      box-sizing: border-box;

      .el-table--striped
        .el-table__body
        tr.el-table__row--striped.el-table__row--striped.el-table__row--striped
        td {
        background-color: #f5f5f5;
      }
    }

    .page-box {
      display: flex;
      justify-content: space-between;
    }
  }
}
