



























































































@import '@/styles/variables.scss';
.tree-box {
  width: 100%;
  height: 100%;
  overflow: auto;
  .tree-title {
    width: 100%;
    height: 24px;
    line-height: 24px;
    padding-left: 20px;
    background: #e4efff;
    color: $menuActiveText;
  }

  .not-active {
    color: #999999;
  }

  .ke-active {
    .icon-gongsi {
      color: #f6aa7b;
    }
    .icon-zuzhi {
      color: #40d8e5;
    }
  }

  .icon-box {
    margin-right: 5px;
  }
  /deep/ .el-tree {
    display: flex;
  }
  /deep/ .el-tree-node > .el-tree-node__children {
    overflow: visible;
  }
  /deep/ .el-tree-node__content {
    display: block;
  }
  /deep/ .el-tree-node {
    min-width: 100%;
  }
}
