






































.promptOperate-box {
  /deep/ .el-dialog {
    width: 280px;
    margin-top: 29% !important;
  }
  /deep/ .el-dialog__header {
    padding: 0;
    padding-bottom: 0;
    height: 28px;
    line-height: 28px;
    background: #4b77be;
  }
  /deep/ .el-dialog__title {
    line-height: 28px;
    font-size: 14px;
    color: #ffffff;
    padding-left: 15px;
  }
  /deep/ .el-dialog__headerbtn {
    top: 4px;
  }
  /deep/ .el-dialog__body {
    padding: 0;
  }
  /deep/ .el-dialog__footer {
    padding: 0 15px 12px;
  }
  .content-box {
    position: relative;
    .cancel-box {
      width: 20px;
      height: 20px;
      position: absolute;
      top: -25px;
      right: 15px;
      background: #4b77be;
      color: #ffffff;
      font-size: 16px;
      line-height: 20px;
      cursor: pointer;
    }
    .message-box {
      padding: 15px 15px 0;
      .message {
        font-size: 12px;
        line-height: 34px;
      }
    }
  }
}
